import React, { useState } from 'react';
import { IonHeader, IonToolbar, IonButtons, IonMenuButton, IonTitle, IonContent, IonPage, IonList, IonInput } from '@ionic/react';
import { Location } from "app/schemas/schema";
import { createStructuredSelector } from "reselect";
//import './ChatView.scss';
import { connect } from "react-redux";
import { getLoading } from "../../selectors/selector";
import { compose } from "redux";
import HTTP from "app/services/httpServices";


interface OwnProps { }

interface StateProps {
  locations: Location[];
  mapCenter: Location;
}

interface DispatchProps { }

interface MapViewProps extends OwnProps, StateProps, DispatchProps { };

const ChatView: React.FC<MapViewProps> = ({ locations, mapCenter }) => {

  const [message, setMessage] = useState<any>([]);
  const [messages, setMessages] = useState<any>([]);
  var token: any;
  // (new HTTP()).__request("https://chat-api.imi.chat/authorize", {
  //   "Content-Type": "application/json", "auth_code": "YmQ1OTU1MTYtZjFhNC00ZGY1LTllZWQtZjdiMTgwMTNkMGUzQHNjaG5laWRlci11YXQuaW1pLmNoYXQ="},"get",null,"",{},0,null).then(function(response:any){
  // console.log(response);

  //})
 
  function getChat() {
    (new HTTP()).__request("https://chat-api.imi.chat/v3.0/chats/CSRYPUYATO2C29WD", {
      "Content-Type": "application/json", "access_token": "aef25744-a5a4-406d-8010-23fc8835cafd"
    }, "get", null, "", {}, 0, null).then(function (result: any) {
     setMessages(result.data.response.messages);
     var element: any = document.getElementById("chatContainer");
     element.scrollTop = element.scrollHeight - element.clientHeight;
    })
  }

  setTimeout(function(){getChat()},messages.length ? 5000 : 0);

  function handleChat(event: any) {
    event.preventDefault();
    setMessage("");

    (new HTTP()).__request("https://chat-api.imi.chat/v3.0/chats/CSRYPUYATO2C29WD/messages", {
      "Content-Type": "application/json", "access_token": "aef25744-a5a4-406d-8010-23fc8835cafd"
    }, "post", JSON.stringify([{ "type": "inbound", "text": message, "timestamp": (new Date()).toJSON() }]), "", {}, 0, null).then(function (response: any) {

      getChat();
     
    })


    return false;
  }
  return (
    <IonPage id="map-view">
      <IonContent class="map-page">
        <div id="chatContainer" style={{ overflow: "auto", height: 'calc(100% - 60px)', padding: '15px', backgroundColor: '#eee' }}>
          {messages.map((msg: any, index: number) => {
            var isBound = /outbound/.test(msg.type);
            return msg.type !== 'announcement' && (<IonList key={index} style={{ marginBottom: '15px', backgroundColor: '#eee' }}>
              <div style={{ textAlign: isBound ? 'left' : 'right', float: isBound ? 'left' : 'right', borderRadius: '10px', display: 'table-cell', padding: '10px', border: '1px solid #bbb', backgroundColor: isBound ? '#ffe5d3' : '#ccc' }}>
                <div style={{ fontWeight: 'bold' }}>{msg.signature ? msg.signature.sender : 'You'} :</div>
                <div>{msg.text}</div>
                <div>{msg.timestamp}</div>
              </div>
            </IonList>)
          })}
        </div>
        <form style={{ border: "1px solid #969494", backgroundColor: "#d2d2d2", float: "right", width: '100%' }} onSubmit={(event) => handleChat(event)}>
          <button id="send" onClick={(event) => handleChat(event)} style={{ float: "right", width: '60px', height: '40px', margin: '5px' }}> Send </button>
          <IonInput type="text" name="chat" value={message} onIonChange={(event: any) => setMessage(event.target.value)} style={{ width: "250px", height: '40px', float: 'right', margin: '5px', border: '1px solid #ccc', padding: '10px', backgroundColor: '#fff' }} />
        </form>
      </IonContent>
    </IonPage>
  )
};



const mapStateToProps = createStructuredSelector({
  locations: () => ([]),
  mapCenter: () => ({ id: 1, name: 'Woodbridge, NJ', lat: 40.5499632, lng: -74.3534314 })
});

const withConnect = connect(
  mapStateToProps
);

export default compose(
  withConnect
)(React.memo(ChatView));

